
interface ButtonProps {
    children: React.ReactNode,
    type?: 'submit' | 'reset' | 'button',
    classname?: string,
    onClick?: () => void
}

export const Button = ({children, type, classname, onClick}: ButtonProps) => {
    return (
        <button className={classname} type={type} onClick={onClick}>
            {children}
        </button>
    )
}